import { createRouter, createWebHashHistory } from 'vue-router';
import Home from './components/Home.vue';
import RandomCommander from './components/RandomCommander.vue';
import ScryfallSearchQuery from './components/ScryfallSearch2/QueryBuilderCodemirror.vue';

const routes = [
  { path: '/', name: 'Home', component: Home },
  { path: '/random-commander', name: 'Random Commander', component: RandomCommander },
  { path: '/scryfall-search', name: 'Scryfall Search', component: ScryfallSearchQuery }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;