export class QueryParser {
  constructor(grammar, data) {
    this.grammar = grammar;
    this.data = data;
    this.tokens = [];
    this.index = 0;
    this.fields = Object.entries(this.grammar.fields)
      .reduce((acc, [k,v]) => {
        acc[k] = v;
        if(v.shorthand) {
          acc[v.shorthand] = v;
        }
        return acc;
      }, {}); 
    this.fieldNames = Object.entries(this.fields).map(([k,v]) => k);
  }

  parse(input) {
    this.index = 0;
    this.tokens = this.tokenize(input);
    const result = this.parseQuery();
    if (this.index < this.tokens.length) {
      throw new Error("Unexpected token: " + this.tokens[this.index]);
    }
    return result;
  }
  
  tokenize(input) {
    const regex = /\s*("(?:\\"|[^"])*"|\(|\)|[^\s]+)/g;   
    const tokens = [];
    let match = regex.exec(input);
  
    while (match) {
      let token = match[1];
  
      // // If it's a quoted string, strip the surrounding quotes
      // if (token.startsWith('"') && token.endsWith('"')) {
      //   token = token.slice(1, -1);
      // }
  
      if (token.trim()) {
        tokens.push(token.toLowerCase());
      }
      match = regex.exec(input);
    }
  
    return tokens;
  }

  parseQuery() {
    const expressions = [];
    expressions.push(this.parseExpression());

    while (this.match('and', 'or')) {
      const operator = this.previous();
      const right = this.parseExpression();
      expressions.push({ type: 'binary', left: expressions.pop(), operator, right });
    }

    return expressions.length === 1 ? expressions[0] : { type: 'query', expressions };
  }

  parseExpression() {
    if (this.match('not')) {
      const operator = this.previous();
      const operand = this.parseTerm();
      return { type: 'unary', operator, operand };
    }

    if (this.match('(')) {
      const query = this.parseQuery();
      this.consume(')');
      return query;
    }

    return this.parseTerm(); 
  }

  parseTermRecurse(field, keyword) {
    const value = this.consume();
    const node = { type: "term", keyword: keyword, field: field, value };
    if (value && this.match("and", "or")) {
      const op = this.previous();
      const curr = this.current();
      if(!this.fieldNames.includes(curr) && curr !== "not") {
        const right = this.parseTermRecurse(field, keyword);
        return { type: 'binary', left: node, operator: op, right };
      } else {
        this.rewind();
      }
    }   
    return node;
  }

  parseTerm() {
    if(this.matchTypes(this.fieldNames)) {
      let field = this.previous();
      let keyword = '';
      const keywords = this.fields[field].keywords?.map(c => c.toLowerCase());
      if(keywords) {
        if(this.matchTypes(keywords)) {
          keyword = this.previous();
        }
      } else {      
        if(this.fields[field].mappedField) {
          keyword = field;
          field = this.fields[field].mappedField;
        }
      }
      if(keyword) {
        return this.parseTermRecurse(field, keyword);
      }
    }

    throw new Error(`Unexpected token: ${this.peek()}`);
  }

  peekTypes(types) {
    if (this.isAtEnd()) return false;
    for (const type of types) {
      if (this.check(type)) {
        return true;
      }
    }
    return false;
  }

  matchTypes(types) {
    if (this.isAtEnd()) return false;
    for (const type of types) {
      if (this.check(type)) {
        this.advance();
        return true;
      }
    }
    return false;
  }

  match(...types) {
    return this.matchTypes(types);
  }

  check(type) {
    if (this.isAtEnd()) return false;
    return this.current() === type;
  }

  rewind() {
    this.index--;
    return this.current();
  }

  advance() {
    if (!this.isAtEnd()) {
      this.index++;
    }
    return this.previous();
  }

  peek() {
    if(!this.isAtEnd()) {
      this.index++;
    }
    const curr = this.current();
    this.rewind();
    return curr;
  }

  current() {
    return this.tokens[this.index];
  }

  previous() {
    return this.tokens[this.index - 1];
  }

  consume(type) {
    if (this.isAtEnd()) {
      throw new Error("Unexpected end of input");
    }
    const token = this.tokens[this.index++];
    if (type && token !== type) {
      throw new Error(`Expected '${type}' but found '${token}'`);
    }
    return token;
  }

  isAtEnd() {
    return this.index >= this.tokens.length;
  }
}
