import { grammar } from "./grammar";
import { data } from "./data";

const keywords = [...new Set(Object.entries(grammar.fields).map(([k,v]) => v.keywords).flat().filter(s => s))];
const fieldsByName = Object.entries(grammar.fields)
  .reduce((acc, [k,v]) => {
    //let field = v.mappedField ? grammar.fields[v.mappedField] : v;        
    v.name = v.mappedField ? v.mappedField : k;
    const field = v;
    //v.name = k;
    acc[k] = field;
    if(v.shorthand) {
      acc[v.shorthand] = v;
    }
    return acc;
  }, {}); 

const keywordDetails = {
  "is": "exactly the specified value(s)",
  "includes": "has the specified value(s)",
  "for": "usable for specified value(s)"
};

function buildFields ()  {
  // return ["class", "constant", "enum", "function", "interface", "keyword", "method", "namespace", "property", "text", "type", "variable"]
  //     .map(s => ({ label: s, type: s}));
  const entries = Object.entries(grammar.fields)
    .map(([k,v]) => ({ 
      label: k, 
      type: "class",
      detail: v.detail
    }));
  return entries;
}

function buildValueSuggestions (fieldName)  {
  const field = fieldsByName[fieldName]?.name ?? fieldName;  
  const section = { name: `${["color", "identity"].includes(field) ? "color" : field} values` };
  if(field === 'keyword') {
    return data.keywordAbilities.map(s => ({ label:s, type: "class", section: section }));
  }

  if(field === 'color' || field === 'identity') {
    return data.colorSuggestions.map(s => ({
      label: s.value,
      displayLabel: s.label,
      type: "class", 
      section: section
    }));
  }

  if(data[field]) {    
    return data[field].map(s => ({
      label: s,
      type: "class",      
      section: section
    }));
  }

  return [];
}

function buildSuggestions(tokens) {
  if (tokens.length === 0) {
    return buildFields();
  }

  const lastToken = tokens[tokens.length - 1];
  const previousToken = tokens[tokens.length - 2];
  const previousPreviousToken = tokens[tokens.length - 3];
  if(!previousToken){
    return buildFields();
  }

  if(fieldsByName[previousToken]) {
    if (fieldsByName[previousToken].mappedField) {
      return buildValueSuggestions(fieldsByName[previousToken].mappedField);
    } else if(fieldsByName[previousToken].keywords) {
      return fieldsByName[previousToken].keywords.map(s => ({label:s, type: "keyword", detail: keywordDetails[s] }));
    } 
  }

  if(keywords.includes(previousToken)) {
    const field = fieldsByName[previousToken] ? fieldsByName[previousToken].name : tokens[tokens.length - 3];
    return buildValueSuggestions(field);
  }

  if (['and', 'or'].includes(previousToken)) {
    let field = '';
    for(let i = tokens.length - 2; i >= 0; i --) {
      if(keywords.includes(tokens[i]) || tokens[i] === "has"){
        field = tokens[i] === "has" ? "keyword" : tokens[i -1];
        break;
      }
    }
    let suggestions = buildFields();
    if(field)
      suggestions = suggestions.concat(buildValueSuggestions(field));
    return suggestions ;
  }    

  if(previousPreviousToken && (keywords.includes(previousPreviousToken) || fieldsByName[previousPreviousToken]?.name)) {
    return [ {label: "and", type: "class" }, {label: "or", type: "class" }];
  }

  return [];
}
  
export function scrydslCompletions(context) {
  const word = context.matchBefore(/\w*/);
  const textUntilPosition = context.state.sliceDoc(0, context.pos);
  const tokens = textUntilPosition.split(/\s+/);
  const suggestions = buildSuggestions(tokens);
  if(!suggestions || suggestions.length === 0)
    return null;
  
  return {
    from: word?.from ?? context.pos,
    options: suggestions
  };
}