import { LRLanguage, LanguageSupport } from "@codemirror/language";
import { styleTags, tags as t } from "@lezer/highlight";
import { foldNodeProp, foldInside, indentNodeProp } from "@codemirror/language";
import { scrydslCompletions } from "./scrydsl-autocomplete";
import { parser as scrydslParser } from "./scrydsl-parser";

export const parser = scrydslParser.configure({
  props: [
    styleTags({
      Identifier: t.string,
      String: t.string,
      "( )": t.paren,
      Keyword: t.operator,
      Field: t.keyword,
      SingleField: t.keyword,
      Value: t.string,
      "and or": t.string,
      }),
      indentNodeProp.add({
      Application: (context) => context.column(context.node.from) + context.unit,
      }),
      foldNodeProp.add({
      Application: foldInside,
    }),
  ],
});

const language = LRLanguage.define({
  parser: parser,
  languageData: {
    autocomplete: scrydslCompletions,
  },
});

export function scrydsl() {
  return new LanguageSupport(language);
}
