import {EditorView} from "@codemirror/view"

export const scrydslTheme = EditorView.theme({
  ".cm-tooltip": {
    color: "#abb2bf"
  },
  ".cm-tooltip-autocomplete": {
    "& > ul > li[aria-selected]": {
      backgroundColor: "#2c313a",
      color: "#fff",
      fontWeight: "bold",
    }
  },
  ".cm-tooltip-autocomplete .cm-completionDetail": {
    color: "#5c6370",
    marginLeft: "12px",
    fontStyle: "italic",
  }
}, {dark:true});