<template>
  <div class="app-container">
    <h1>Scryfall Query Builder</h1>
    <div class="">       
      <div class="editor-container">       
        <div ref="editor"></div>
      </div>

      <button class="button" @click="generateScryfallQuery">Generate Query</button>
      
    </div>
    <div class="output" v-if="scryfallQuery">
        <div>
          <div><strong>Scryfall Query:</strong></div> 
          <div class="query-box">{{ scryfallQuery }}</div>
        </div>
        <div>
          <div><strong>Link:</strong></div>        
          <div class="query-box">
            <a :href="scryfallLink" target="_blank">{{ scryfallLink }}</a>
          </div>
        </div>
      </div>
      <div v-if="parseError" class="error">
        {{ parseError }}
      </div>
  </div>
</template>

<script>
import { onMounted, ref } from "vue";
import { EditorView, basicSetup } from "codemirror";
import { EditorState } from "@codemirror/state";
import { oneDark } from "@codemirror/theme-one-dark";
import { scrydslTheme } from "./scrydsl-theme";
import { scrydsl, parser } from './scrydsl-lang';
import { QueryParser } from './parser';
import { grammar } from './grammar';
import { data } from './data';
import { QueryConverter } from './queryConverter';

export default {
  name: "LightweightEditor",
  props: {
    modelValue: String,
  },
  emits: ["update:modelValue"],
  setup(props, { emit }) {
    const editor = ref(null);
    const editorState = ref(null);
    const query = ref('');
    const scryfallLink = ref('');
    const parseError = ref('');
    const scryfallQuery = ref('');
    const queryConverter = new QueryConverter(grammar, data);

    onMounted(() => {
      const state = EditorState.create({
        doc: props.modelValue || "",
        extensions: [
          basicSetup,
          scrydsl(),
          scrydslTheme,
          oneDark,
          EditorView.updateListener.of((update) => {
            if (update.changes) {
              const newValue = update.state.doc.toString();
              query.value = newValue;
              emit("update:modelValue", newValue);
            }
          }),
        ],
      });
      
      editorState.value = state;

      new EditorView({
        state,
        parent: editor.value,
      });
    });

    
    const generateScryfallQuery = () => {
      try {
        const parser = new QueryParser(grammar, data);
        const ast = parser.parse(query.value); 

        if (!ast) {
          throw new Error('Invalid query structure.');
        }

        scryfallQuery.value = queryConverter.convert(ast);
        scryfallLink.value = `https://scryfall.com/search?q=${encodeURIComponent(scryfallQuery.value)}`;
        parseError.value = '';
      } catch (error) {
        scryfallQuery.value = '';
        scryfallLink.value = '';
        parseError.value = `Error parsing query: ${error.message}`;
      }
    };

    return { editor, scryfallQuery, scryfallLink, parseError, generateScryfallQuery  };
  },
};
</script>

<style>

.editor-container {
  position: relative;
  margin-bottom: 10px;
}

.button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.button:hover {
  background-color: #0056b3;
}

.output {
  margin-top: 20px;
  padding: 15px;
 
  border: 1px solid #44475a;
  border-radius: 8px;
  font-size: 14px;
  color: #f8f8f2;
}

.output div {
  margin-bottom: 10px;
}

.output .query-box {
  background-color: #282c34;
  padding: 10px;
  border-radius: 5px;
  font-family: 'Courier New', Courier, monospace;
  white-space: pre-wrap;
  overflow-x: auto;
  color: #f8f8f2;
}

.output a {
  color: #8be9fd;
  text-decoration: none;
  font-weight: bold;
}

.output a:hover {
  text-decoration: underline;
}

.error {
  margin-top: 20px;
  padding: 10px;
  background-color: #ff5555;
  color: #f8f8f2;
  border: 1px solid #ff3333;
  border-radius: 8px;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.error::before {
  content: "⚠️";
  font-size: 16px;
}




.cm-variableName {
  color: #ff79c6; /* Color for variable names */
}

.cm-bool {
  color: #ffb86c; /* Color for boolean values */
}

.cm-string {
  color: #f1fa8c; /* Color for string literals */
}

.cm-lineComment {
  color: #6272a4; /* Color for line comments */
}

.cm-keyword {
  color: #8be9fd; /* Color for keywords */
}

.cm-atom {
  color: #50fa7b; /* Color for fields */
}

.cm-literal {
  color: #f8f8f2; /* Color for values */
}

/* You can also customize the background of the editor 
.cm-content {
  background-color: #282a36;
  color: #f8f8f2;
}
*/
</style>