export const grammar = {
  fields: {
    color: {  
      shorthand: "c",
      keywords: ['is', 'includes' ],
      translations: { is: "c=", includes: "c>=" },
    },
    identity: {  
      shorthand: "id",
      keywords: ['is', 'includes', 'for', ],
      translations: {  is: "id=", includes:"id>=", for: "id<=",  },
    },
    type: {  
      shorthand: "t",
      keywords: ['is'],
      translations: { is: "t:" },
    },
    
    costs: {  
      keywords: ['>', '>=', '=', '<=', '<'],
      translations: { '>': "cmc>", '>=':"cmc>=", '=':"cmc=", '<=':"cmc<=", '<':"cmc<" },
    },
    power: {  
      keywords: ['>', '>=', '=', '<=', '<'],
      translations: { '>': "pow>", '>=':"pow>=", '=':"pow=", '<=':"pow<=", '<':"pow<" },
    },
    toughness: {  
      keywords: ['>', '>=', '=', '<=', '<'],
      translations: { '>': "tou>", '>=':"tou>=", '=':"tou=", '<=':"tou<=", '<':"tou<" },
    },
       
    rarity: {  
      keywords: ['is'],
      translations: { is: "rarity:" },
    },   
    legal: {  
      shorthand: "l",
      keywords: ['in'],
      translations: { in: "f:" },
    },
    game: {  
      shorthand: "g",
      keywords: ['is'],
      translations: { is: "game:" },
    },
    card: {      
      keywords: ['is'],
      translations: { is: "is:" },
    },

    name: {  
      keywords: ['is', 'contains', 'startsWith', 'endsWith'],
      translations: { 
        is(value) { return `/^${value.replace(/"/g, "")}$/`;}, 
        contains(value) { return value.includes("%") ? `/${value.replace(/%/g, ".*").replace(/"/g, "")}/` : `${value}`; }, 
        startswith(value) { return `/^${value.replace(/"/g, "")}/`;}, 
        endswith(value) { return `/${value.replace(/"/g, "")}$/`;},
      },
    },
    
    oracletext: {  
      shorthand: "o",
      keywords: ['is', 'contains', 'startsWith', 'endsWith'],
      translations: { 
        is(value) { return `o:/^${value.replace(/"/g, "")}$/`;}, 
        contains(value) { return value.includes("%") ? `o:/${value.replace(/%/g, ".*").replace(/"/g, "")}/` : `o:${value}`; }, 
        startswith(value) { return `o:/^${value.replace(/"/g, "")}/`;}, 
        endswith(value) { return `o:/${value.replace(/"/g, "")}$/`;},
       },
    },

    from: {
      detail: "set",
      mappedField: "set",
      translations: { from: "set:" }
    },    
    set: {  
      keywords: ['is'],
      translations: { is: "set:", from: "set:" },
    },

    has: {
      detail: "keyword",
      mappedField: "keyword",
      translations: { has: "keyword=" }
    },
    keyword: {  
      ignore: true,
      translations: { has: "keyword=" },
    },
  },
  operators: {
    and: "AND",
    or: "OR",
    not: "NOT"
  }
};
  